import React, { useState, useEffect } from "react";
import NewsTab from "../PageElements/NewsTab";
import Container from "@material-ui/core/Container";
import Grid from "@material-ui/core/Grid";
import WatchlistContainer from "../PageElements/WatchlistContainer";
import { connect } from "react-redux";
import { setSelectedWatchlistIndex } from "./Watchlist/actions";

const mapDispatchToProps = (dispatch) => {
  return {
    setSelectedWatchlist: (index) => dispatch(setSelectedWatchlistIndex(index)),
  };
};

const mapStateToProps = (state) => {
  return {
    isUserLoggedIn: state.loginStatus.userIsLoggedIn,
    watchlists: state.userDataObj.userData.watchlists,
    events: state.userDataObj.userData.events,
    isUserDataPending: state.userDataObj.isPending,
    selectedWatchlist: state.selectedWatchlistIndex.index,
    isDataRetrieved: Boolean(
      !state.userDataObj.isPending &&
        state.loginStatus.userIsLoggedIn &&
        state.userDataObj.userData.watchlists
    ),
  };
};

const WatchlistPage = ({ watchlists, isDataRetrieved, selectedWatchlist }) => {
  useEffect(() => {
    if (isDataRetrieved)
      console.log("~!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!", watchlists);
  }, [isDataRetrieved]);
  return (
    <Container maxWidth="lg" disableGutters={true}>
      <Grid item lg>
        <WatchlistContainer />
      </Grid>

      <Grid container direction="row" wrap="nowrap">
        <Grid item zeroMinWidth xs></Grid>
        <Grid item zeroMinWidth xs>
          <NewsTab
            header
            stockSymbols={
              isDataRetrieved ? watchlists[selectedWatchlist].symbols : []
            }
          ></NewsTab>
        </Grid>
      </Grid>
    </Container>
  );
};

export default connect(mapStateToProps, mapDispatchToProps)(WatchlistPage);
