import {
  SET_SELECTED_INDEX,
  ADD_WATCHLIST_PENDING,
  ADD_WATCHLIST_SUCCESS,
  ADD_WATCHLIST_FAILED,
  ADD_SYMBOL_PENDING,
  ADD_SYMBOL_SUCCESS,
  ADD_SYMBOL_FAILED,
  DELETE_WATCHLIST_PENDING,
  DELETE_WATCHLIST_SUCCESS,
  DELETE_WATCHLIST_FAILED,
  REMOVE_SYMBOL_PENDING,
  REMOVE_SYMBOL_SUCCESS,
  REMOVE_SYMBOL_FAILED,
} from "./constants";

import axios from "axios";

import { SERVER_URL } from "../../Essential";

axios.defaults.withCredentials = true;

export const setSelectedWatchlistIndex = (index) => ({
  type: SET_SELECTED_INDEX,
  payload: index,
});

export const addWatchlists = (username, watchlistName) => (dispatch) => {
  dispatch({ type: ADD_WATCHLIST_PENDING });

  let url = SERVER_URL + "/watchlist/add";

  fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({ username: username, watchlistName: watchlistName }),
  })
    .then((resp) => resp.json())
    .then((resp) =>
      dispatch({ type: ADD_WATCHLIST_SUCCESS, payload: resp.watchlistName })
    )
    .catch((error) => {
      dispatch({ type: ADD_WATCHLIST_FAILED, payload: error });
    });
};

export const addSymbol = (username, watchlistName, symbol) => (dispatch) => {
  dispatch({ type: ADD_SYMBOL_PENDING });

  let url = SERVER_URL + "/watchlist/addSymbol";

  fetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify({
      username: username,
      watchlistName: watchlistName,
      symbol: symbol,
    }),
  })
    .then((resp) => resp.json())
    .then((resp) => dispatch({ type: ADD_SYMBOL_SUCCESS, payload: resp }))
    .catch((error) => {
      dispatch({ type: ADD_SYMBOL_FAILED, payload: error });
    });
};

