import {
  SET_SELECTED_INDEX,
  ADD_WATCHLIST_PENDING,
  ADD_WATCHLIST_SUCCESS,
  ADD_WATCHLIST_FAILED,
  ADD_SYMBOL_PENDING,
  ADD_SYMBOL_SUCCESS,
  ADD_SYMBOL_FAILED,
  DELETE_WATCHLIST_PENDING,
  DELETE_WATCHLIST_SUCCESS,
  DELETE_WATCHLIST_FAILED,
  REMOVE_SYMBOL_PENDING,
  REMOVE_SYMBOL_SUCCESS,
  REMOVE_SYMBOL_FAILED,
} from "./constants";

import omit from "lodash/omit";

const initWatchlistIndex = {
  index: 0,
};

export const selectedWatchlistIndex = (
         state = initWatchlistIndex,
         action = {}
       ) => {
         switch (action.type) {
           case SET_SELECTED_INDEX:
             return Object.assign({}, state, { index: action.payload });
           default:
             return state;
         }
       };
